<template>
         <div class="pagination">
           <span  @click="gopage(page-1)" :class="page>1?'':'distab'">上一页</span>
           <span class="pageshelect">
               <span>第{{page}}页</span>
              <div class="pagelist">
                <ul>
                  <li v-for="i in totalpage" :class="i == page?'pageselected':''" @click="gopage(i)">第{{i}}页</li>
                </ul>
              </div>
           </span>
           <span  :class="page<totalpage?'':'distab'" @click="gopage(page+1)">下一页</span>
         </div>
</template>

<script>

export default {
  props:["page","totalpage"],
  data () {
    return {

    }
  },
  mounted:function () {

  },

  methods:{
    gopage:function (page) {
      if(page == this.page || page>this.totalpage || page<1){
        return
      }
      this.$emit("changepage",page);
    },

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


.pagination{
  background-color: #fff;
  text-align: center;
  margin-bottom:20px;
}
.pagination>span{
    height: 35px;
    line-height: 35px;
    display: inline-block;
    width: 100px;
  }
.pagination>span:hover{
  color: #eb7350;
}
.pageselected{
  color: #eb7350;
}
.pagination>span:last-child{
  border: none;
}
.pagination .pageshelect{
  position: relative;
}
.pageshelect>span{
  padding-left: 25px;
  padding-right: 25px;
  border-right: solid 1px #ccc;
  border-left: solid 1px #ccc;
}
.pageshelect:hover  .pagelist{
  display: inline-block;
}
 .pagelist:hover{
   display: inline-block;
 }
.pagination .distab,.pagination .distab:hover{
  color: #aaa;
}
  .pagelist{
    display: none;
    position: absolute;
    z-index: 99;
    bottom: 30px;
    left: 10px;
    max-height: 280px;
    overflow-y: auto;
    overflow-x: hidden;
    background: #fff;
    color: #333;
    border: 1px solid #ccc;
    border-radius: 2px;
    width: 80px;
    box-shadow: 0px 2px 8px 1px rgba(0,0,0,0.2);
  }
.pagelist ul li:hover{
  color: #eb7350;

}

</style>



